.container {
  display: block;
  width: 320px;
  margin: 0 auto;
}


.form {
  max-width: 300px;
  border: 1px solid #000;
  padding: 10px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  .input {
    display: block;
    margin-bottom: 10px;
    height: 20px;
    border-radius: 2px;
    box-sizing: border-box;
    border: 1px solid #ced4da;


    &:hover,
    &:focus {
      color: #495057;
      outline: 0;
      border: 1px solid rgb(28, 211, 221);
      border-color: #80bdff;
      box-shadow: 0 0 0 0.1rem rgba(0, 123, 255, .25);
    }

  }

  .button {
    width: 100px;
    height: 20px;
    cursor: pointer;
    border-radius: 2px;
    box-sizing: border-box;
    border: none;
    outline: 0;

    &:hover {
      background-color: #007AFF; // rgba(0, 123, 255, .25)
    }
  }
}


.filter {
  .input {
    display: block;
    margin-bottom: 10px;
    height: 20px;
    box-sizing: border-box;
    outline: 0;
    border-radius: 2px;
    border: 1px solid #ced4da;

    &:hover {
      border: 1px solid rgb(28, 211, 221);
      border-color: #80bdff;
      box-shadow: 0 0 0 0.1rem rgba(0, 123, 255, .25);
    }
  }
}

.contactList {
  display: flex;
  flex: 0 0 300px;
  flex-wrap: wrap;
  justify-content: space-between;


  li {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    flex: 0 0 250px;
    margin-bottom: 10px;

    .button {
      font-size: 10px;
      width: 50px;
      height: 20px;
      cursor: pointer;
      border-radius: 5px;
      border: none;
      box-sizing: border-box;


      &:hover,
      &:focus {
        background-color: rgb(0, 123, 255);
      }
    }
  }
}